/* === CNMC minimal facelift (Sky theme) === */
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;600;700&display=swap');

:root{
  --text:#0f172a;
  --muted:#475569;
  --card:rgba(255,255,255,.72);
  --line:rgba(15,23,42,.10);
  --blue:#2563eb;
  --blue2:#38bdf8;
}

html,body{ height:100%; }
body{
  margin:0;
  font-family:"Noto Sans TC","Microsoft JhengHei",system-ui,-apple-system,sans-serif;
  color:var(--text);
  line-height:1.8;
  /*
  background:
    linear-gradient(180deg, rgba(240,249,255,.92) 0%, rgba(224,242,254,.92) 35%, rgba(255,255,255,.98) 100%),
    url("/bsky.33c8ef83.jpg"); *//* 你站內的天空圖 :contentReference[oaicite:2]{index=2} */
  background-size:cover;
  background-attachment:fixed;
}

/* 版面收斂：不改內容，先讓閱讀舒服 */
main, article, .content, .container, body > div{
  max-width: 980px;
  margin: 0 auto;
  padding: 24px 16px 72px;
}

/* 導覽列：黏在上面、玻璃感 */
nav, header{
  position: sticky;
  top: 0;
  z-index: 50;
  backdrop-filter: blur(10px);
  background: rgba(255,255,255,.65);
  border-bottom: 1px solid var(--line);
}

/* 標題階層 */
h1{
  font-size: 44px;
  margin: 18px 0 6px;
  letter-spacing: .5px;
}
h2{
  font-size: 26px;
  margin: 34px 0 12px;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--line);
}
h3{ font-size: 18px; margin: 18px 0 8px; }
p{ color: var(--muted); margin: 0 0 14px; }

/* 連結改乾淨 */
a{ color: var(--blue); text-decoration: none; }
a:hover{ text-decoration: underline; }

/* 圖片：圓角 + 陰影 + 自動置中（你現在很多裸圖） :contentReference[oaicite:3]{index=3} */
img{
  max-width:100%;
  height:auto;
  border-radius: 16px;
  box-shadow: 0 12px 30px rgba(2,6,23,.18);
  display:block;
  margin: 14px auto;
}

/* 區塊卡片化：你不需要重做排版，先把每段視覺統一 */
section, .card{
  background: var(--card);
  border: 1px solid var(--line);
  border-radius: 18px;
  padding: 18px 18px;
  margin: 18px 0;
}

/* 分隔線變漂亮（如果你之後把 * * * 換成 <hr> 會更佳） */
hr{
  border: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(37,99,235,.35), transparent);
  margin: 18px 0;
}

/* 小小的按鈕樣式（給你未來放「加入/了解更多」用） */
.btn{
  display:inline-flex; align-items:center; justify-content:center;
  padding: 10px 14px;
  border-radius: 12px;
  border: 1px solid rgba(37,99,235,.22);
  background: rgba(255,255,255,.70);
  box-shadow: 0 10px 24px rgba(2,6,23,.10);
  gap: 8px;
}
.btn.primary{
  background: linear-gradient(135deg, var(--blue), var(--blue2));
  color: white;
  border-color: transparent;
}
